import React from 'react';
import './NavbarSide.css';

function NavbarSide(props) {
  return (
    <div
      id='navbarside-container'
      style={{
        visibility: props.opened ? 'visible' : 'hidden',
        opacity: props.opened ? 1 : 0,
      }}
    ></div>
  );
}

export default NavbarSide;
