import React from 'react';
import './ProgressBar.css';
import boat from './assets/boat.svg';
import { ReactComponent as ManSitting } from './assets/man_sitting.svg';
import { ReactComponent as ManStanding } from './assets/man_standing.svg';

class ProgressBar extends React.Component {
  constructor(props) {
    super(props);
    // this.showFishMan = this.showFishMan.bind(this)
    this.fishingRod = this.fishingRod.bind(this);
  }

  componentDidMount() {
    document.querySelector('#man_sit').classList.add('active');
  }

  showFishMan() {
    if (this.props.reachEnd) {
      if (document.querySelector('#man_stand')) {
        setTimeout(() => {
          document.querySelector('#man_stand').classList.add('active');
          document.querySelector('#boat').classList.add('come');
        }, 5);
      }
    } else {
      if (document.querySelector('#man_sit')) {
        setTimeout(() => {
          document.querySelector('#man_sit').classList.add('active');
          document.querySelector('#boat').classList.remove('come');
        }, 5);
      }
    }
  }

  fishingRod() {
    var percentageToLeft = 0;

    if (document.querySelector('#pages-container')) {
      percentageToLeft =
        this.props.lastScrollTop /
        (document.querySelector('#pages-container').offsetWidth -
          window.innerWidth);
    }
    if (document.querySelector('#line_1')) {
      document.querySelector('#line_1').style.transform = `scale(${
        0.3 + 0.7 * percentageToLeft
      })`;
    }
  }

  render() {
    return (
      <div id='progressbar-container'>
        {this.props.reachEnd ? <ManStanding /> : <ManSitting />}
        {this.showFishMan()}
        <img src={boat} alt='' id='boat' />
        {this.fishingRod()}
      </div>
    );
  }
}

export default ProgressBar;
