import React from 'react';
import logo from "./assets/JP Space.svg";
import logoFilled from "./assets/JP Space_filled.svg";
import './NavbarDefault.css';

function NavbarDefault(props) {
  return( 
  <div id="container">
      {props.opened ? 
        <div style={{opacity : 0}}>
          <img id="img-logo" src={logo} alt="" />
          <img id="img-logo-filled" src={logoFilled} alt="" />  
        </div>
        : 
        <div>
          <img id="img-logo" src={logo} alt="" />
          <img id="img-logo-filled" src={logoFilled} alt="" />  
        </div>}
      
      {props.opened ? 
        <div className="hamburger menu-opened" onClick={props.toggleOpened}>
          <div className="top-bun menu-opened"></div>
          <div className="meat menu-opened"></div>
          <div className="bottom-bun menu-opened"></div>
        </div>
        :  
        <div className="hamburger" onClick={props.toggleOpened}>
          <div className="top-bun"></div>
          <div className="meat"></div>
          <div className="bottom-bun"></div>
        </div>
      }
  </div>)
}

export default NavbarDefault;
