import React from 'react';
import './ScrollDownAnimation.css';

function ScrollDownAnimation(props) {
  return (
      props.scrollDown ? (
        <div className='scroll-animation-container'>
            <div className='chevron'></div>
            <div className='chevron'></div>
            <div className='chevron'></div>
            <span className='scroll-animation-text'>Scroll down</span>
          </div>
      ) : (
        <div className='scroll-animation-container scroll-up'>
            <div className='chevron scroll-up'></div>
            <div className='chevron scroll-up'></div>
            <div className='chevron scroll-up'></div>
            <span className='scroll-animation-text scroll-up'>Scroll Up</span>
          </div>
      )
  );
}

export default ScrollDownAnimation;
