import React from 'react';
import { Link } from 'react-router-dom';
import './Pages.css';
import Button from 'react-bootstrap/Button';
import ScrollDownAnimation from './ScrollDownAnimation.js';
import ProgressBar from './ProgressBar.js';
import { ReactComponent as Developper } from './assets/developper_junha.svg';
import { ReactComponent as InvestmentBoard } from './assets/investment_board.svg';
import ComingSoon from './assets/coming_soon.jpg';

class Pages extends React.Component {
  constructor() {
    super();
    this.state = {
      scrollDown: true,
      reachEnd: false,
      lastScrollTop: 0,
    };
    this.scrollListen = this.scrollListen.bind(this);
  }

  componentDidMount() {
    window.document.querySelector('body').style.overflowX = 'scroll';
    window.document.querySelector('body').style.overflowY = 'hidden';
    window.addEventListener('scroll', this.scrollListen);
  }
  componentWillUnmount() {
    window.document.querySelector('body').style.overflowX = 'hidden';
    window.document.querySelector('body').style.overflowY = 'scroll';
    window.removeEventListener('scroll', this.scrollListen);
  }

  scrollListen() {
    var st = window.scrollX; // Credits: "https://github.com/qeremy/so/blob/master/so.dom.js#L426"
    console.log(st);
    console.log(this.state.reachEnd);
    if (st < 10) {
      this.setState({
        scrollDown: true,
        reachEnd: false,
      });
      console.log(this.state.reachEnd);
    } else if (
      Math.abs(
        st +
          window.innerWidth -
          document.querySelector('#pages-container').offsetWidth
      ) < 10
    ) {
      this.setState({
        scrollDown: false,
        reachEnd: true,
      });
    } else if (st > this.state.lastScrollTop) {
      this.setState({ scrollDown: true });
    } else {
      this.setState({ scrollDown: false });
    }
    this.setState({ lastScrollTop: st <= 0 ? 0 : st });
  }

  render() {
    return (
      <div id='pages-container' onScroll={this.scrollListen}>
        <section>
          <div id='main-container'>
            <div id='text-container'>
              <p data-aos='fade-right' data-aos-duration='2000'>
                Fullstack Developer
              </p>
              <h1
                data-aos='fade-right'
                data-aos-delay='500'
                data-aos-duration='2000'
              >
                Discover
                <br /> My Passion in Software Development
              </h1>
              <Link to='/resume'>
                <Button
                  variant='outline-light'
                  size='lg'
                  to='/resume'
                  data-aos='fade-right'
                  data-aos-delay='1000'
                  data-aos-duration='2000'
                >
                  Get Resume
                </Button>
              </Link>
            </div>
            <Developper
              data-aos='fade-up'
              data-aos-delay='1500'
              data-aos-duration='2000'
            />
          </div>
        </section>
        <section>
          <div id='text-container'>
            <p data-aos='fade-right' data-aos-duration='2000'>
              Lifelong Investor
            </p>
            <h1
              data-aos='fade-right'
              data-aos-delay='500'
              data-aos-duration='2000'
            >
              Discover
              <br /> My Passion in Portfolio Management
            </h1>
            <Button
              variant='outline-light'
              size='lg'
              data-aos='fade-right'
              data-aos-delay='1000'
              data-aos-duration='2000'
            >
              Coming Soon
            </Button>
          </div>
          <InvestmentBoard
            data-aos='fade-top'
            data-aos-delay='1500'
            data-aos-duration='2000'
          />
        </section>
        <section>
          <div id='text-container'>
            <p>Leisure Gamer</p>
            <h1>
              Discover
              <br /> My Passion in League of Legends
            </h1>
            <Button variant='outline-light' size='lg'>
              Coming Soon
            </Button>
          </div>
          <img src={ComingSoon} width='400' />
        </section>
        <ScrollDownAnimation scrollDown={this.state.scrollDown} />
        <ProgressBar
          reachEnd={this.state.reachEnd}
          lastScrollTop={this.state.lastScrollTop}
        />
      </div>
    );
  }
}

export default Pages;
